@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
.container-fluid{
  width: 90%;
  margin: auto;
}
h1{
  font-weight: bold;
}
.table-responsive{
  max-height: 700px;
  box-shadow: rgba(0,0,0,0.25) 0px 25px 50px -12px;
}
tr:hover{
  background-color: #ffa;
}
td, th{
  position: relative;
}
td:hover::after, 
th:hover::after{
  content: "";
  position: absolute;
  background-color: #f2f2f2;
  left: 0;
  top: -5000px;
  height: 5000px;
  width: 100%;
  z-index: -1;
}

.table tr td:hover:nth-child(4){
  background-color: rgb(231, 83, 115);
  color: aliceblue;
  border: 0.5px solid rgb(12,12,12)
}
.table tr td:hover{
  background-color: rgb(117,223,122);
  color: aliceblue;
  border: 0.5px solid rgb(12, 12,12);
}
.thead-dark{
  font-weight: bold;
  text-transform: uppercase;
}

 h2{
  margin-top: 10px;
  color: #000;
  text-align: center;
}
h2{
  -webkit-animation: live 2s ease-out infinite;
          animation: live 2s ease-out infinite;
}
@-webkit-keyframes live {
  0% {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  }
  50% {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  }
  100% {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  }
  }
@keyframes live {
  0% {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  }
  50% {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  }
  100% {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  }
  }
